import request from '@/utils/request'

export function getList(query) {
  return request(({
    url: '/dynamic/list',
    method: 'get',
    params: {
      page: query.page,
      size: query.size
    }
  }))
}

export function create(data) {
  return request(({
    url: '/dynamic/create',
    method: 'post',
    data:data
  }))
}
export function getHandleList(id){
  return request({
    url: `/dynamic/getHandleList`,
    method: 'get',
    params:{topicId: id}
  })
}
export function handleList(data){
  return request({
    url: `/dynamic/handleList`,
    method: 'post',
    data:data
  })
}
export function upload(file){
  return request(({
    url: '/upload/dynamic',
    method: 'post',
    data: file
  }))
}
export function uploadList(file){
  return request(({
    url: '/upload/dynamicList',
    method: 'post',
    data: file
  }))
}
export function deleteDynamic(url){
  return request(({
    url: '/upload/deleteDynamic',
    method: 'get',
    params: {path:url}
  }))
}