<script>
import {getList,create,upload,deleteDynamic,handleList,getHandleList} from "@/api/sparetime";

export default {
  name: "idle",
  data() {
    return {
      query: {
        page: 1,
        size: 10,
        type: {
          String,
          default: 'random'
        }
      },
      content:'',
      circleUrl:"http://127.0.0.1:8000/upload/dynamic/2021-10-07/23df0708-a6c6-483d-94a8-21f9e04c5e6a.jpg",
      list: 4,
      dialog: 0,
      dialogImageUrl: '',
      flag:true,
      disabled: false,
      dialogVisible: false,
      file:new FormData(),
      fileList: [],
      reList:[]
    }
  },
  created() {
    this.fetchList();
  },
  watch:{
    content(){
      this.flag = this.content.length <= 0;
    },
    fileList(){
      this.dialog=this.fileList.length
      this.disabled = this.fileList.length > 0;
    }
  },
  methods: {
    up(){
      const arr=[]
      for(let dy of this.fileList){
        arr.push(dy.url)
      }
      const dynamic = {
        content: this.content,
        images:arr
      }
     create(dynamic).then(res=>{
       console.log(res)
     })
    },
    fetchList() {
      getList(this.query).then((res) => {
        console.log(res)
        const {data} = res
        this.reList=data.records
      });
    },

    // 板块
    handleList(type) {
      getList(type, this.query).then(() => {
        this.list = 1;
      });
    },
    beforeUpload(file){
      console.log(file)
    },
    upload(file){
      const data=new FormData()
      console.log(file)
      data.append("file",file.file)

      upload(data).then(res=>{
        console.log(res)
        this.fileList.push({
          name:file.file.uid,
          url:res.data
        })
      })
    },
    handleRemove(file, fileList) {
      console.log(file)
      for (let i=0;i<this.fileList.length;i++){
       if (this.fileList[i].name===file.raw.uid){
         deleteDynamic(this.fileList[i].url).then(res=>{
           console.log(res)
           this.fileList.splice(i,1)
         })
       }
      }
    },
    handlePreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handlePre(url){
      this.dialogImageUrl = url;
      this.dialogVisible = true;
    },
    change(file, fileList){
      console.log(file);
    },

    handleLike(id,da,e){

      let flag=true;
      const a=e.target.classList.contains("active")
      if (a){
        flag=!flag
      }
      const data={
        topicId:id,
        handleType:da,
        done:flag
      }
      handleList(data).then((res)=>{
        console.log(res)
        if (a){
          e.target.innerText=parseInt(e.target.innerText)-1
          e.target.classList.remove("active")
        }else {
          e.target.innerText=parseInt(e.target.innerText)+1
          e.target.classList.add("active")
        }
      }).catch(()=>{})
    },
  }
}
</script>

